import React, { useRef, useState } from "react";
import { Link } from "react-router-dom";
import Footer from "../../../components/Footer";
import Header from "../../../components/Header";
import Sidebar from "../../../components/Sidebar";
import firebase, { db } from "../../../config/Firebase";
import Icon from "../../../utils/Icon";
import Input from "../../../utils/Input";

const Testimonial = ({ history }) => {
  const [name, setName] = useState("");
  const [src, setSrc] = useState("");
  const [stars, setStars] = useState("");
  const [review, setReview] = useState("");
  const form = useRef(null);
  const handleSubmit = (e) => {
    e.preventDefault();
    if (stars <= 5) {
      form.current.classList.add("show_on_success");
      db.collection("testimonials")
        .add({ name, src, stars, review })
        .then(() => {
          form.current.classList.remove("show_on_success");
          setName("");
          setSrc("");
          setStars(null);
          setReview("");
        })
        .catch((error) => {
          form.current.classList.remove("show_on_success");
        });
    } else {
    }
  };
  const handleClick = () => {
    firebase
      .auth()
      .signOut()
      .then(() => history.push("/"))
      .catch((error) => {});
  };
  return (
    <div className="add_panel">
      <Header admin={true} />
      <Sidebar admin={true} />
      <div className="outer_container">
        <form onSubmit={handleSubmit} className="adding_form container">
          <div className="loader_success" ref={form}>
            <Icon loader={true} />
          </div>
          <h1 id="litheader" className="light_heading">
            ADD NEW REVIEW
          </h1>
          <div className="row">
            <Input
              onChange={(e) => setName(e.target.value)}
              value={name}
              type="text"
              id="name"
              placeholder="Name"
            />
            <Input
              onChange={(e) => setSrc(e.target.value)}
              value={src}
              type="url"
              id="image"
              placeholder="Image"
            />
          </div>
          <div className="row">
            <Input
              onChange={(e) => setStars(e.target.value)}
              value={stars}
              type="number"
              id="stars"
              placeholder="Stars (1 - 5)"
            />
            <div className="col-lg-6  col-12 ">
              <label htmlFor="review">Review</label>
              <div className="btn_animate input_animate">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <textarea
                  required
                  style={{ height: `auto` }}
                  id="review"
                  onChange={(e) => setReview(e.target.value)}
                  value={review}
                  placeholder="Review"
                ></textarea>
              </div>
            </div>
          </div>
          <div className="center btn_wrapper">
            <button className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Add new Review
              <span className="shift">›</span>
            </button>
            <Link to="/" className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Home
            </Link>
            <div
              className="btn_animate"
              style={{ display: `inline-block` }}
              onClick={handleClick}
            >
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Logout &nbsp;
              <span className="shift">›</span>
            </div>
          </div>
        </form>
        <Footer />
      </div>
    </div>
  );
};

export default Testimonial;
