import React, { FC, InputHTMLAttributes } from "react";
interface Props {
  id: string;
}
const Input: FC<Props & InputHTMLAttributes<HTMLInputElement>> = (props) => {
  return (
    <div className="col-lg-6 col-12">
      <label htmlFor={props.id}>{props.id}:</label>
      <div className="btn_animate input_animate">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <input
          required
          {...props}
          autoComplete="off"
          className="browser-default"
        />
      </div>
    </div>
  );
};

export default Input;
